.progress__name {
	font-size: 14px;
	font-family: "Raleway";
	color: $white;
	font-weight: 700;
	text-transform: uppercase;
	line-height: 1.429;

	margin: 10px 0;
}

.progress__element {
	border: 1px solid rgba(255, 255, 255, 0.25);
	border-radius: 3px;

	padding: 2px;
}

.progress__line {
	background-color: $white;

	width: 0;
	height: 4px;

	position: relative;
}

.progress__tooltip {
	background-color: $primary;
	position: absolute;

	border-radius: 3px;

	right: -15px;
	top: -32px;

	width: 31px;
	z-index: 255;

	font-size: 11px;
	color: $white;
	font-weight: bold;
	line-height: 18px;
	text-align: center;

	display: none;

	&::before {
		content: "";
		border: 4px solid transparent;
		border-top: 4px solid $primary;

		position: absolute;
		bottom: -7px;
		left: 50%;

		transform: translateX(-50%);
	}
}
